@font-face {
  font-family: "Paperlogy-Bold";
  font-weight: normal;
  src: url("../font/Paperlogy-7Bold.ttf") format("truetype");
}

.navigator-container {
    width: 100%;
    height: 10%;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15);
    padding: 0 10%;
    box-sizing: border-box;
    font-size: 28px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navigator-title {
    font-family: 'Paperlogy-Bold';
    text-align: left;
    width: fit-content;
    display: flex;
    align-items: center;
    height: 100%;
}

.navigator-menu-box {
    display: flex;
    gap: 1.5rem;
}

/* TODO 메뉴 모듈화 */
.navigator-menu {
    text-align: right;
    width: fit-content;
    display: flex;
    align-items: center;
    height: 100%;
    font-size: 22px;
}

.navigator-hambuger-toggle {
    height: 30%;
    width: 10%;
    display: none;
}
.navigator-hambuger-toggle > div {
    display:block;
    width:100%;
    height:5px;
    border-radius: 30px;
    background-color: black;
    transition:all .35s;
}

.hambuger-top-line {
    top: 0;
}

.hambuger-middle-line {
    top:50%;
}

.hambuger-bottom-line {
    bottom: 0;
}

@media (max-width : 991px) {
    .navigator-menu-box {
        display: none;
    }
    .navigator-container {
        padding: 0 2% 0 5%;
        font-size: 18px;
    }
    .navigator-hambuger-toggle{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}